*{
  margin:0;
  padding:0;
}
body{
  background-image: url("./imgs/background.jpg");
}
.App {
  text-align: center;
}
.title{
  border-bottom: 1px solid black;
  
}

.Container{
 display:grid;
 grid-template-columns: 1fr 1.5fr 1fr;
grid-row-gap: 2em;

}
.Container a {
  text-decoration: none;
}
.PFitems{
  background-color: rgba(255, 255, 255, 0.479);
    grid-column: 2/2;
    padding:3%;
    border-radius: 0.5em;
    -webkit-border-radius: 0.5em;
    -moz-border-radius: 0.5em;
    -ms-border-radius: 0.5em;
    -o-border-radius: 0.5em;
}
.PFitems a{
  background-color: rgb(174, 179, 174);
  padding:0.2em;
  border-radius: 0.3em;
  -webkit-border-radius: 0.3em;
  -moz-border-radius: 0.3em;
  -ms-border-radius: 0.3em;
  -o-border-radius: 0.3em;
  color:rgb(0, 0, 0);
}
.PFitems a:hover{
  background-color: rgb(188, 192, 188);
}
/*CSS för About*/
.Container-index{
  display:grid;
  grid-template-columns: 1fr 2fr 2fr 1fr;
}
.column-index{
  padding: 1em;
  background-color: rgba(255, 255, 255, 0.479);
  grid-column: 2/4;
  border-radius: 0.5em;
  display: flex;
  text-align: center;
  -webkit-border-radius: 0.5em;
  -moz-border-radius: 0.5em;
  -ms-border-radius: 0.5em;
  -o-border-radius: 0.5em;
}
/*CSS för CV*/
.Container-CV {
  display:grid;
  grid-template-columns: 1fr 0.2fr 1fr;
  grid-template-rows: 1fr 2fr 2fr;
  grid-row-gap: 1em;
  grid-template-areas:     
  "column1 c column"
  "column3 c column2"
  "column3 c column4";
  background-color: rgba(255, 255, 255, 0.479);
  border-radius: 0.5em;
  margin:0.5em;
  padding:1em
}
.Column1-CV{
  grid-area: column1;
  padding-left: 1em;
}
.Column2-CV{
  grid-area:column2;
  padding-left: 1em;
}
.Column3-CV{
  grid-area: column3;
  padding-left: 1em;
}
.Column4-CV{
  grid-area: column4;
  padding-left: 1em;
}
.cv-text h4{
  font-style: italic;
}
/*CSS för Contact*/
.Container-contact{
  display: grid;
  grid-template-columns: 1fr 2fr 1fr;
}
.Column-contact{
  grid-column: 2/2;
  text-align: center;
  background-color: rgba(255, 255, 255, 0.479);
  border-radius: 0.5em;
  padding: 1em;
}
/*CSS för filler*/
.Container-filler{
  display: grid;
  grid-template-columns: 1fr 3fr 1fr 3fr 1fr;
}
.Column1-filler{
  grid-column: 2/2;
  background-color:  rgba(255, 255, 255, 0.479);
  padding:1em;
  text-align: center;
  border-radius: 1em;
}
.Column2-filler{
  grid-column: 4/4;
  background-color:  rgba(255, 255, 255, 0.479);
  padding:1em;
  text-align: center;
  border-radius: 1em;
  font-family: 'Tilt Warp';
  font-size: 1.1em;
}
/*CSS för navbar*/
.Nav {
  overflow: hidden;
  display: flex;
  list-style-type: none;
  margin: 0;
  padding:0;
  justify-content: center;
}

.Nav li:hover {
  background-color:  rgba(255, 255, 255, 0.479);
}
.Nav li{
  float:left;
}
.Nav li a {
  display:block;
  color:rgb(0, 0, 0);
  text-align:center;
  padding:0.5em;
  text-decoration:none;
}
.ActiveSite {
  text-decoration: underline;
}
@media(max-width:700px) {
  .Nav{
      flex-direction: column;
  }
  .Nav a, a:visited{
      font-size:1em;
  }
  .Container-portfolio{
      display:grid;
      grid-template-columns: 1fr 2fr 1fr;
      grid-template-rows: 1fr 1fr 1fr 1fr;
      grid-template-areas: 
      "column1 column1 column1"
      "column2 column2 column2"
      "column3 column3 column3"
      "column4 column4 column4"
      "column5 column5 column5";
      grid-row-gap: 2em;
  }
  .column1-pf{
      grid-area: column1;
      grid-column-start: 2;
      grid-column-end: 2;
  }
  .column2-pf{
      grid-area: column2;
      grid-column-start: 2;
      grid-column-end: 2;
  }
  .column3-pf{
      grid-area:column3;
      grid-column-start: 2;
      grid-column-end: 2;
  }
  .column4-pf{
      grid-area:column4;
      grid-column-start: 2;
      grid-column-end: 2;
  }
  .column5-pf{
      grid-area:column5;
      grid-column-start: 2;
      grid-column-end: 2;
  }
  .column6-pf{
      grid-area:column6;
      grid-column-start: 2;
      grid-column-end: 2;
  }
  .Container-index{
      display:grid;
      grid-template-columns: 1fr 2fr 1fr;
  }
  .column-index{
      
     grid-column: 2/2;
      text-align: center;
      display: inline-block
  }

  .Container-filler{
      display: grid;
      grid-template-columns: 0.5fr 4fr 0.5fr;
      grid-template-rows: 1fr;
      grid-row-gap: 1em;
      grid-template-areas: 
      "txt1 txt1 txt1"
      "txt2 txt2 txt2";
  }
  .Column1-filler{
     grid-area:txt1;
     grid-column: 2/2;
  }
  .Column2-filler{
      grid-area:txt2;
      grid-column: 2/2;
  }
  .Container-CV {
      display:grid;
      grid-template-columns: 1fr 3fr 1fr;
      grid-template-rows: 1fr;
      grid-template-areas:     
      "column1 column1 column1"
      "column2 column2 column2"
      "column3 column3 column3"
      "column4 column4 column4";
  }
  .Column1-CV{
      grid-area: column1;
      grid-column: 2/2;
  }
  .Column2-CV{
      grid-area:column2;
      grid-column: 2/2;
  }
  .Column3-CV{
      grid-area: column3;
      grid-column: 2/2;
  }
  .Column4-CV{
      grid-area: column4;
      grid-column: 2/2;
  }
}